<template>
    <div>
        <el-dialog title="添加" :visible.sync="dialogdetailisible" @close="closeDialog" width="40%">
            <el-form ref="form" :model="form" label-width="100px" :rules="rules">
                <el-form-item label="账号" prop="gid">
                    <el-input v-model="form.gid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色" prop="role">
                    <el-checkbox-group v-model="form.role">
                        <el-checkbox
                            v-for="r in roleLists"
                            :key="r.rid"
                            :label="r.name"
                            name="role"
                        ></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="状态">
                    <el-switch v-model="form.state"></el-switch>
                </el-form-item>
                <el-form-item style="text-align: right;">
                    <el-button type="primary" @click="onSubmit">提交</el-button>
                    <el-button @click="dialogdetailisible=false;">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        var validateId = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("请输入管理员账号"));
            }
            let id = value;
            var format = /^[0-9]/;
            //号码规则校验
            if (!format.test(id)) {
                return callback(new Error("只能输入数字"));
            }
            callback();
        };
        return {
            dialogdetailisible: false,
            roleLists: [],
            form: { gid: "", role: [], state: true },
            rules: {
                gid: [
                    {
                        required: true,
                        validator: validateId,
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    props: {
        detailVisible: Boolean
    },
    created: function() {
        this.getList();
    },
    watch: {
        detailVisible(newValue, oldValue) {
            this.dialogdetailisible = newValue;
        }
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/roleLists"
            })
                .then(function(response) {
                    let data = response.data;
                    here.roleLists = data;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        //添加管理员
        onSubmit() {
            let here = this;
            this.$refs.form.validate(valid => {
                if (valid) {
                    let f = 1;
                    console.log(here.form.role);
                    for (let i = 0; i < here.form.role.length; i++) {
                        if (i != ",") {
                            f = 0;
                            break;
                        }
                    }
                    if (f === 0) {
                        axios({
                            method: "post",
                            url: "/gladda",
                            data: here.form
                        })
                            .then(function(response) {
                                if (response.data.success === 1) {
                                    here.$message({
                                        message: "修改成功",
                                        type: "success"
                                    });
                                    here.from = { role: [], state: true };
                                } else if (response.data.success === 3) {
                                    here.$message.error("您没有权限!");
                                } else {
                                    here.$message.error("添加失败!");
                                }
                                here.dialogdetailisible = false;
                            })
                            .catch(function(error) {
                                console.log(error);
                            });
                    } else {
                        here.$message.error("必须选择角色!");
                    }
                }
            });
        }
    }
};
</script>

<style lang="stylus" scoped></style>


